import React from 'react';

class Console extends React.Component {

  render() {
    return (
      <div id="console">
        <div id='console-prompt'>
          {this.getMessage()}:
        </div>
        <div id='console-input'>
          {this.props.currentLine}
        </div>
        <div></div>
        <span className="console-output-container">
          <span className="console-container">
            <div className='console-label'>History</div>
            <span className="console-group">
                {this.props.history.map((val, i) => {
                  return (
                    <div key={'history' + val + i}>
                      <span>{val}</span>
                    </div>
                  );
                })}
            </span>
          </span>
          <span className="console-container">
            <div className='console-label'>Output</div>
            <span className="console-group">
              {this.props.rovers.map((rover, i) => {
                if (!rover.isDoneMoving) return '';
                return (
                  <div key={`rover-output-${i}`}>
                      {rover.x + ' '+ rover.y + ' ' + rover.direction}
                  </div>
                );
              })}
            </span>
          </span>
        </span>
      </div>
    );
  }

  getMessage() {
    switch(this.props.stateName){
      case 'build':
        return '$ build plateau (x, y)';
      case 'deploy':
        return '$ deploy rover (x, y, N|S|E|W)'
      case 'move':
        return '$ move rover (L|R|M)'
      default: return '';
    }
  }


}

export default Console;
