class StateManipulator {

  static stateNames = [ // identifiers for state
    'build',
    'deploy',
    'move',
  ];

  static getNextState = (state, char) => {
    // always check global state transitions first
    let applicableTransitions = StateManipulator.stateTransitions.global;
    applicableTransitions = applicableTransitions.concat(
      StateManipulator.stateTransitions[state.stateName][state.statePhase]
    );

    for (let i in applicableTransitions) {
      let res = applicableTransitions[i](state, char);
      if (!res) continue;
      return Object.assign({}, state, res);
    }
    return null;
  }


  // ABOUT
  // each state is defined as a key on this object
  // a character results in 1 or 0 valid transitions
  // each key has a 2 dimensional array
  //   1st dimension: all possible transitions for that state
  //   2nd dimension: all possible transitions for that phase
  // note: 'global' isn't a state, it's a set of transitions for all states and
  //   therefore is the only one-dimensional array
  //
  // INPUT (all functions have the same signature)
  // state: current data associated with state
  // char: the last character (keyboard input) pressed
  //
  // RETURN
  // null (char is not a valid transition)
  // object containing updates to the state (to be used with Object.assign...)
  static stateTransitions = {
    'global': [
      (state, char) => { // char is backspace and input !== '' --> prev state
        if(char !== '\b' || state.input.length === 0) return null;
        return state.stateHistory.slice().pop();
      }
    ],
    'build': [
      [ // state: build0
        (state, char) => { // /\d/ --> build1
          if (!/\d/.test(char)) return null;
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[0],
            statePhase: 1,
          }, state, char);
        },
      ],
      [ // state: build1
        (state, char) => { // /\d/ --> build1
          if (!/\d/.test(char)) return null;
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[0],
            statePhase: 1,
          }, state, char);
        },
        (state, char) => { // / / --> build2
          if (!/ /.test(char)) return null;
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[0],
            statePhase: 2
          }, state, char);
        },
      ],
      [ // state: build2
        (state, char) => { // /\d/ --> build3
          if (!/\d/.test(char)) return null;
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[0],
            statePhase: 3,
          }, state, char);
        },
      ],
      [ // state: build3
        (state, char) => { // /\d/ --> build3
          if (!/\d/.test(char)) return null;
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[0],
            statePhase: 3,
          }, state, char);
        },
        (state, char) => { // /[\r|\n]/ --> deploy0
          if (!/[\r|\n]/.test(char)) return null;
          let [w, h] = (state.input).split(' ').map((v,i) => {
            return parseInt(v, 10);
          });
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[1],
            statePhase: 0,
            dimensions: {
              width: w+1,
              height: h+1,
            }
          }, state, char, true);
        },
      ]
    ],
    'deploy': [
      [ // state: deploy0
        (state, char) => { // /\d/ --> deploy1
          if (!/\d/.test(char)) return null;
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[1],
            statePhase: 1,
          }, state, char);
        },
      ],
      [ // state: deploy1
        (state, char) => { // /\d/ --> deploy1
          if (!/\d/.test(char)) return null;
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[1],
            statePhase: 1,
          }, state, char);
        },
        (state, char) => { // /[\r|\n]/ --> deploy2
          if (!/ /.test(char)) return null;
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[1],
            statePhase: 2,
          }, state, char);
        },
      ],
      [ // state: deploy2
        (state, char) => { // /\d/ --> deploy3
          if (!/\d/.test(char)) return null;
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[1],
            statePhase: 3,
          }, state, char);
        },
      ],
      [ // state: deploy3
        (state, char) => { // /\d/ --> deploy3
          if (!/\d/.test(char)) return null;
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[1],
            statePhase: 3,
          }, state, char);
        },
        (state, char) => { // / / --> deploy4
          if (!/ /.test(char)) return null;
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[1],
            statePhase: 4,
          }, state, char);
        },
      ],
      [ // state: deploy4
        (state, char) => { // /[n|s|e|w]/i --> deploy5
          if (!/[n|s|e|w]/i.test(char)) return null;
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[1],
            statePhase: 5
          }, state, char);
        },
      ],
      [ // state: deploy5
        (state, char) => {  // /[\r|\n]/ --> move0
          if (!/[\r|\n]/.test(char)) return null;
          let [x, y, dir] = (state.input).split(' ').map((v,i) => {
            if (i === 2) return v;
            return parseInt(v, 10);
          });

          // DO NOT finalize state changes
          // if the user enters invalid dimensions and hits enter
          // this state should essentially start over at phase 0
          if (x >= state.dimensions.width || y >= state.dimensions.height) {
            let newInputHistory = state.inputHistory.slice();
            newInputHistory.push(state.input);
            return {
              inputHistory: newInputHistory,
              stateName: StateManipulator.stateNames[1],
              statePhase: 0,
              input: '',
            };
          }

          let newRovers = state.rovers.slice();
          newRovers.push({
            x: x,
            y: y,
            direction: dir,
            isDoneMoving: false,
          });
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[2],
            statePhase: 0,
            rovers: newRovers,
          }, state, char, true);
        }
      ]
    ],
    'move': [
      [ // state move0
        (state, char) => {  // /[l|r|m]/i --> move0
          if (!/[l|r|m]/i.test(char)) return null;
          let allRovers = state.rovers.slice();
          let thisRoverIndex = state.rovers.length - 1;

          // set new rover variables (make sure not to null out previous)
          let x = allRovers[thisRoverIndex].x;
          let y = allRovers[thisRoverIndex].y;
          let newDirection = allRovers[thisRoverIndex].direction;

          if (/m/i.test(char)) {
            [x, y] = StateManipulator.getNextLocation(
              state, allRovers[thisRoverIndex]
            );
          } else {
            newDirection = StateManipulator.getNextDirection(
              allRovers[thisRoverIndex], /l/i.test(char)
            );
          }

          allRovers[thisRoverIndex] = {
            x: x,
            y: y,
            direction: newDirection,
            isDoneMoving: false,
          }
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[2],
            statePhase: 0,
            rovers: allRovers,
          }, state, char);
        },
        (state, char) => {  // /[\r|\n]/ --> deploy0
          if (!/[\r|\n]/.test(char)) return null;
          let allRovers = state.rovers.slice();
          allRovers[state.rovers.length - 1].isDoneMoving = true;
          return StateManipulator.finalizeStateChanges({
            stateName: StateManipulator.stateNames[1],
            statePhase: 0,
            rovers: allRovers,
          }, state, char, true);
        }
      ]
    ],
  }

  // ABOUT
  // functionality done at the end of ALMOST every state transition
  //
  // INPUT
  // state: current data associated with state
  // char: the last character (keyboard input) pressed
  // goToNextState: true if transition caused stateName to change
  //
  // RETURN
  // new state object, updated as needed
  //
  static finalizeStateChanges =
    (newState, oldState, char, goToNextState=false) => {
    let newStateHistory = oldState.stateHistory.slice();
    newStateHistory.push(oldState);

    // update state history
    // reset input
    // update input history
    if (goToNextState) {
      let newInputHistory = oldState.inputHistory.slice()
      newInputHistory.push(oldState.input)
      return Object.assign({}, newState, {
        input: '',
        stateHistory: newStateHistory,
        inputHistory: newInputHistory,
      });
    }
    // update state history
    // update input
    else
      return Object.assign({}, newState, {
        input: oldState.input + char,
        stateHistory: newStateHistory,
      });
  }
  static getNextLocation = (state, rover) => {
    let xMod = 0;
    let yMod = 0;
    switch (rover.direction) {
      case 'N':
        yMod = 1;
        break;
      case 'S':
        yMod = -1;
        break;
      case 'E':
        xMod = 1;
        break;
      case 'W':
        xMod = -1;
        break;
      default:
        break;
    }

    let ret = [rover.x + xMod, rover.y + yMod];
    if (ret[0] < 0 || ret[0] > state.dimensions.width - 1 ||
        ret[1] < 0 || ret[1] > state.dimensions.height - 1)
      return [rover.x, rover.y]
    return ret
  };

  static getNextDirection = (rover, turnLeft) => {
    let directions = ['N', 'W', 'S', 'E'];
    if (!turnLeft) directions.reverse()

    let nextDirIndex = directions.indexOf(rover.direction) + 1;
    nextDirIndex = nextDirIndex % directions.length;

    return directions[nextDirIndex]
  };
}
 export default StateManipulator;
