import React from 'react';
import Plateau from './plateau.js';
import Console from './console.js';
import StateManipulator from './stateManipulator.js'

class Rover extends React.Component {

  constructor(props) {
    super(props);
    this.state =  {
      data: {
        rovers: [], // rovers deployed (object, keys: x, y, dir, isDone)
        inputHistory: [], // user input history (strings)
        stateHistory: [], // history of states (object)
        dimensions: { // tracks dimensions of plateau (first input)
          width: 0,
          height: 0,
        },
        input: '', // current line of user input
        stateName: StateManipulator.stateNames[0], // current state name
        statePhase: 0, // current state phase (index)
      }
    }
    document.addEventListener("keydown", this.handleKeydown, false);
  }

  render() {
    return (
      <div className="rover">
        <Plateau
          dimensions={this.state.data.dimensions}
          rovers={this.state.data.rovers}
        />
        <Console
          history={this.state.data.inputHistory}
          currentLine={this.state.data.input}
          rovers={this.state.data.rovers}
          stateName={this.state.data.stateName}
        />
      </div>
    );
  }

  componentWillUnmount() {
    document.removeEventListener("keydown", this.handleKeydown, false);
  }

  handleKeydown = (e) => {
    this.setState((state) => {
      let char = String.fromCharCode(e.which);
      let nextState = StateManipulator.getNextState(state.data, char);
      if (!nextState) return
      return { data: nextState };
    });
  };
}

export default Rover;
