import React from 'react';
import FontAwesome from 'react-fontawesome';

class Plateau extends React.Component {


  render() {
    if (!this.props.dimensions.width || !this.props.dimensions.height)
      return null;

    return (
      <div className="plateau">
        { Array(this.props.dimensions.width).fill(1).map(this.getRow) }
      </div>
    );
  }

  // to be used in map function (see render), val is always 1
  getRow = (val, x) => {
    return (
      <span className="plateau-row" key={'row'+x}>
        { Array(this.props.dimensions.height).fill(x).map(this.getCol).reverse() }
      </span>);
  };

  // to be used in map function (see render), x is row num
  getCol = (x, y) => {
    return (<span className="location" key={x + ',' + y}>
      {this.getRoverAtLocation(x, y)}
    </span>);
  };

  // returns ALL rovers at a given coordinare (x, y)
  // rovers are represented by fontawesome arrows
  getRoverAtLocation = (x, y) => {
    let ret = [];
    for (let i in this.props.rovers) {
      let rover = this.props.rovers[i];
      let cssClass = rover.isDoneMoving ? 'rover-dead' : 'rover-alive'
      if (rover.x !== x || rover.y !== y) continue;
      ret.push(this.getArrow(rover.direction.toUpperCase(), i, cssClass));
    }
    return ret;
  }

  getArrow = (direction, roverIndex, cssClass) => {
    return (
      <FontAwesome
        name={Plateau.arrowDirections[direction]}
        key={'rover'+roverIndex}
        className={cssClass}
      />);
    }

  static arrowDirections = {
      N: 'arrow-up',
      S: 'arrow-down',
      E: 'arrow-right',
      W: 'arrow-left',
  }
}

export default Plateau;
